import { Component, OnInit } from '@angular/core';
import { PersonalService } from './../../shared/personal.service';

declare var $: any;

@Component({
  selector: 'app-mi-perfil-form',
  templateUrl: './mi-perfil-form.component.html',
  styles: []
})
export class MiPerfilFormComponent implements OnInit {
  public session: any;
  private $form;

  statusVisible = false;
  statusMessage;
  perfil: any = {};
  isChanginPassword = false;
  validationOptions = {
    feedbackIcons: {
      valid: 'glyphicon glyphicon-ok',
      invalid: 'glyphicon glyphicon-remove',
      validating: 'glyphicon glyphicon-refresh'
    },
    fields: {
      nombre: {
        validators: {
          notEmpty: {
            message: 'Ingrese un nombre completo'
          }
        }
      },
      email: {
        validators: {
          notEmpty: {
            message: 'Ingrese un correo electrónico'
          },
          emailAddress: {
            message: 'Ingrese un correo electrónico válido'
          }
        }
      }
    }
  };

  constructor(private personalService: PersonalService) {}

  ngOnInit() {
    this.personalService.getMiPerfil()
      .subscribe((data: any) => {
        this.perfil = data.result;
      });
  }

  update(ev) {
    this.perfil[ev.target.name] = ev.target.value;
  }

  save(perfil) {
    if (typeof perfil.newPassword !== 'undefined') {
      perfil['password'] = perfil.newPassword;
    } else {
      delete perfil['password'];
    }
    this.personalService.updatePerfil(perfil)
      .subscribe((data: any) => {
        if (data.success) {
          this.statusMessage = 'Perfil actualizado';
          this.statusVisible = true;
          this.$form.data('bootstrapValidator').resetForm();
        }
      });
  }

  onValidationSuccess(e) {
    this.$form = $(e.currentTarget);
    if (e.type === 'success') {
      this.save(this.perfil);
    }
  }
}



